<template>
  <div>

    <div v-if="character !== null">
      <h2>{{character.name}}</h2>

      <div class="info">

        <img :src="characterImageURL" :alt="character.name">

        <div class="info-text">

          <h3 class="h-description">Description</h3>
          <p>{{character.description.trim() || 'No description available.'}}</p>

          <h3>Comics</h3>
          <p><b>{{character.comics.available}}</b> comics available</p>

          <h3>Favorites</h3>
          <p>
            <!--<button @click="addToFavorites()">Add to Favorites</button>-->
            [Add to Favorites]
          </p>
        </div>
      </div>

      <small>
        <a :href="character.urls[0].url || 'http://marvel.com'">{{attributionText}}</a>
      </small>
    </div>

  </div>
</template>

<script>
//  import firebase from 'firebase';

  export default {
    name: 'character',
    computed: {
      attributionText: function () {
        return this.$store.getters.character.attributionText;
      },
      character: function () {
        if (this.$store.getters.character !== null) {
          return this.$store.getters.character.data.results[0];
        } else {
          return null;
        }
      },
      characterImageURL: function () {
        return this.character.thumbnail.path + '/portrait_incredible.' + this.character.thumbnail.extension;
      },
//      user() {
//        return this.$store.getters.user;
//      },
    },
    beforeCreate: function () {
      this.$store.dispatch('fetchCharacter', this.$route.params.id);
    },
    destroyed: function () {
      this.$store.commit('setCharacter', null);
    },
    methods: {
//      addToFavorites() {
//        const favouriteCharactersRef = firebase.database().ref(`users/${this.user.uid}/favouriteCharacters`);
//        const newFavouriteCharacter = favouriteCharactersRef.push();
//        newFavouriteCharacter.set({
//          characterId: this.$route.params.id,
//          characterName: this.character.data.results[0].name,
//          characterImageURL: this.$store.getters.character.data.results[0].thumbnail.path + '/standard_fantastic.' + this.$store.getters.character.data.results[0].thumbnail.extension,
//        });
//      },
    },
  };
</script>

<style scoped>
  .link {
    color: #000;
    text-decoration: none;
  }

  .link:hover {
    text-decoration: underline;
  }

  .info {
    display: flex;
    flex-wrap: wrap;
  }

  .info-text {
    width: 100%;
  }

  img {
    width: 216px;
    height: 324px;
    flex-shrink: 0;
    background-color: #ccc;
  }

  small {
    margin-top: 20px;
    display: inline-block;
  }

  a {
    color: #989898;
    text-decoration: none;
  }

  button {
    border-radius: 5px;
    border: 1px solid #303030;
    background-color: #303030;
    font-size: 13px;
    padding: 0 5px;
    color: #fff;
    width: 100%;
    line-height: 30px;
    /*max-width: 216px;*/
  }

  @media (min-width: 500px) {

    .info {
      flex-wrap: nowrap;
      justify-content: flex-start;
    }

    .h-description {
      margin-top: 0;
    }

    img {
      margin-right: 30px;
    }

  }
</style>



